import React, { Component } from 'react';
import styled from "styled-components";
import Feliz from './Feliz'; 

const Container = styled.div`
  wdith: 100%;
  max-width: 640px;
  input[type="text"]{
    padding: 9.5px 20px;
    margin-right: 2px;
  }
  button{
    color: #00d8ff;
    background: #433D3D;
    border: none;
    box-shadow: none;
    padding: 10px 30px;
    font-size: 16px;
    transition: .5s;
  }
  button:hover{
    background: #00d8ff;
    color: #433D3D;
  }
`
class App extends Component {
   state = {
    nombre: '', 
    individuos: [{
      nombre: 'milton',
      felicidad: 100
    }] 
  }
  actualizarNombre = (event) => {
    this.setState({nombre: event.target.value})
  }

  obtenerFelicidad = async (event) => {
    event.preventDefault();
    const request = await fetch("https://wt-3581e5a0e6c19bb4a0552203b2738a9d-0.run.webtask.io/obtener-ritmosustancia")
    const response = await request.json()
    this.setState({
      individuos: [...this.state.individuos, {
        nombre: this.state.nombre,
        felicidad: response
      }],
      nombre: '', 
    })
  }
  render() {
    return (
     <Container>
       <h1>Cual es tu felicidad</h1>
       {this.state.individuos.map((individuo) =>
          <Feliz
            nombre={individuo.nombre}
            felicidad={individuo.felicidad}
          />
        )}
       <form onSubmit={this.obtenerFelicidad}>
          <input type="text" placeholder="Ingrese un nombre" onChange={this.actualizarNombre} value={this.state.nombre} />
          <button type="submit">Obtener mi Felicidad</button>
        </form>
     </Container>
    );
  }
}

export default App;



